<script>
import AuthApi from "../../../server/auth/authApi";

export default {
  data() {
    return {
      form: {
        userLoginName: '',
        password: ''
      },
      auth: new AuthApi(),
      showError: false,
    };
  },
  methods: {
    async submit() {
      this.auth.loginApi(this.form).then((res) => {
        if (res) {
          this.$router.push("/");
        }
        else {
          this.showError = true
        }
      });
    },
  },
};
</script>
